import React, {Component} from 'react';
import { Card,Dropdown } from 'react-bootstrap';

class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {
          show: true,
          category: ""
        };
        this.showModal = this.showModal.bind(this);
        this.hideModal = this.hideModal.bind(this);
      }

    showModal = () => {
        this.setState({ show: true });
    };

    hideModal = () => {
        this.setState({ show: false });
    };

    handleCategoryChange = (e) => {
        this.setState({category: e.target.value});
    };

    render() {
        return (
        <form onSubmit={this.hideModal}>
        <Card show={this.state.show}>
            <Card.Header>
            <Card.Title>Trivia</Card.Title>
            </Card.Header>
            <Card.Body>
            <Dropdown 
                onChange={this.handleCategoryChange} 
            >
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                    Dropdown Button
                </Dropdown.Toggle>

                <Dropdown.Menu>
                    <Dropdown.Item value="General Knowledge">General Knowledge</Dropdown.Item>
                    <Dropdown.Item value="Mythology">Mythology</Dropdown.Item>
                    <Dropdown.Item value="Sports">Sports</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
            <p>Press Start to Play</p>
            </Card.Body>
            <Card.Footer>
                <button onClick={this.props.start}>Start</button>
            </Card.Footer>
        </Card>
        </form>
        );
    }
}
   
export default Menu;